.ant-table .headerCell {
  font-weight: 400;
  font-size: 14px;
  background-color: white;
  text-transform: uppercase;
  font-family: 'Poppins';
}

.card-container {
  display: flex;
  justify-content: center;
}

.card-responsive {
  flex: 1;
  max-width: 100%;
}

.left-aligned-cell {
  text-align: left;
  font-weight: 100;
  font-size: 13px;
  font-family: 'Poppins';
}

.right-aligned-cell {
  text-align: right;
  font-weight: 100;
  font-size: 13px;
  font-family: 'Poppins';
}
